import { decryption } from "../Encryption/decrypt";
import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

// all of encrypted api only==========
export const commiteeActivateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/committee/ins/${data.instituteId}/iqac/query`,
      method: POST,
      body: data?.bodyParameter,
    }),
  });
};

export const committeeDashboardMasterFunction = (builder) => {
  return builder.query({
    query: (data) => `/v2/committee/${data.qid}/dashboard`,
    transformResponse: (response) => {
      let dt = decryption(response?.encrypt);
      return dt?.payroll;
    },
  });
};

