export const committeeAuthorityListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v2/committee/${data?.qid}/all/custom/authority?page=${data.page}&limit=${data.limit}&search=${data.search}`,
    // keepUnusedDataFor: 0,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.all_au,
        };
      else
        return {
          page: arg?.page,
          list: response?.all_au,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },

    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

